<template>
   <el-row class="classPC">
     <el-col :span="24" class="top">
   	  <el-col :span="2">
   	  		<img src="../../assets/img/tag.png" >
   				<span class="ctitle">
   					我的课程
   				</span>
   	  </el-col>
   	<!--  <el-col :span="2" :offset="20" class="more">
   	  		更多	  
   	  </el-col> -->
     </el-col>
	 <el-col class="searchCommon">
	 		 <span class="vimg spanCo">
	 			  <img src="../../assets/img/time.png" >
	 		 </span>
	 		 <span class="imgTitle spanCo">
	 			时间排序
	 		 </span>
	 		 <span class="vimg spanCo" @click="sort()">
	 		 	  <img src="../../assets/img/px.png" >
	 		 </span>
	 		 <span class="imgTitle spanCo">课程名称</span>
	 		 <el-col :span="4" class="imput">
	 		 <el-input placeholder="输入相关课程名称" v-model="input">
	 		  </el-input>
	 		 </el-col>
	 		 <el-button type="primary" @click="search"><img src="../../assets/img/search.png" >搜索</el-button>
	 </el-col>
	 <el-col :span="24" class="bottom">
		 <el-col :span="6" :offset="1" class="bbox" v-for="(item,index) in listData" :key="index">
		 		<div class="img">
		 			<img src="../../assets/img/user.jpg" >
					<!-- <span class="imgtext" :class="{'progress':status=='进行中','success':status=='已完成'}">进行中</span> -->
					<div class="totaltime">
						{{item.period}}课时
					</div>
		 		</div>
				<div class="text">
					<div class="desc">
						{{item.cname}}
					</div>
					<div class="descinfo">
						<!-- <span class="left">
							完成进度:30%
						</span> -->
						<span class="right">
							主讲人:{{item.teacher}}
						</span>
					</div>
					<div class="study">
						<el-button type="primary" @click="start(item)">开始学习</el-button>
					</div>
				</div>
		 </el-col>
		 <el-col :span="6" :offset="2" class="bbox" v-show="false">
		 		<div class="img">
		 			<img src="../../assets/img/user.png" >
					<span class="imgtext" :class="{'progress':status1=='进行中','success':status=='已完成'}">未开始</span>
					<div class="totaltime">
						20课时
					</div>
		 		</div>
		 		<div class="text">
		 			<div class="desc">
		 				职业与创业胜任力胜任力…
		 			</div>
		 			<div class="descinfo">
		 				<span class="left">
		 					完成进度:30%
		 				</span>
		 				<span class="right">
		 					主讲人:张朝阳
		 				</span>
		 			</div>
		 			<div class="study">
		 				<el-button type="primary">开始学习</el-button>
		 			</div>
		 		</div>
		 </el-col>
		 <el-col :span="6" :offset="2" class="bbox" v-show="false">
		 		<div class="img">
		 			<img src="../../assets/img/user.png" >
					<span class="imgtext" :class="{'progress':status=='进行中','success':status2=='已完成'}">已完成</span>
					<div class="totaltime">
						20课时
					</div>
		 		</div>
		 		<div class="text">
		 			<div class="desc">
		 				职业与创业胜任力胜任力…
		 			</div>
		 			<div class="descinfo">
		 				<span class="left">
		 					完成进度:30%
		 				</span>
		 				<span class="right">
		 					主讲人:张朝阳
		 				</span>
		 			</div>
		 			<div class="study">
		 				<el-button type="primary">开始学习</el-button>
		 			</div>
		 		</div>
		 </el-col>
		<el-col :span="6" :offset="1" class="bbox" v-show="false">
				<div class="img">
					<img src="../../assets/img/user.png" >
							<span class="imgtext" :class="{'progress':status=='进行中','success':status=='已完成'}">进行中</span>
							<div class="totaltime">
								20课时
							</div>
				</div>
						<div class="text">
							<div class="desc">
								职业与创业胜任力胜任力…
							</div>
							<div class="descinfo">
								<span class="left">
									完成进度:30%
								</span>
								<span class="right">
									主讲人:张朝阳
								</span>
							</div>
							<div class="study">
								<el-button type="primary" @click="start">开始学习</el-button>
							</div>
						</div>
		</el-col>
		<el-col :span="6" :offset="2" class="bbox" v-show="false">
				<div class="img">
					<img src="../../assets/img/user.png" >
							<span class="imgtext" :class="{'progress':status=='进行中','success':status=='已完成'}">进行中</span>
							<div class="totaltime">
								20课时
							</div>
				</div>
						<div class="text">
							<div class="desc">
								职业与创业胜任力胜任力…
							</div>
							<div class="descinfo">
								<span class="left">
									完成进度:30%
								</span>
								<span class="right">
									主讲人:张朝阳
								</span>
							</div>
							<div class="study">
								<el-button type="primary" @click="start">开始学习</el-button>
							</div>
						</div>
		</el-col>
		<el-col :span="6" :offset="2" class="bbox" v-show="false">
				<div class="img">
					<img src="../../assets/img/user.png" >
						<span class="imgtext" :class="{'progress':status=='进行中','success':status=='已完成'}">进行中</span>
						<div class="totaltime">
							20课时
						</div>
				</div>
						<div class="text">
							<div class="desc">
								职业与创业胜任力胜任力…
							</div>
							<div class="descinfo">
								<span class="left">
									完成进度:30%
								</span>
								<span class="right">
									主讲人:张朝阳
								</span>
							</div>
							<div class="study">
								<el-button type="primary" @click="start">开始学习</el-button>
							</div>
						</div>
		</el-col>
	 </el-col>
	 <el-col :span="24" class="cpage">
		 <el-pagination
		   background
		   layout="prev, pager, next"
		   :prev-text='prevT'
		   :next-text='nextT'
		   :current-page.sync='cpage'
		   @current-change="current"
		   :page-size="limit"
		   :total="total">
		 </el-pagination>
	 </el-col>
   </el-row>
</template>

<script>
// import {getSubject}  from '../../api/apis/user.js'
import {GetClass} from '@/api/test/user.js'
export default {
	name:'classPC',
	data(){
		return {
			prevT:'上一页',
			nextT:'下一页',
			status:'进行中',
			status1:'',
			status2:'已完成',
			input:'',
			listData:[],
			cpage:0,
			total:0,
			limit:12,
			orderByCreateTimeAsc:0,
			}
	},
	mounted() {
		let that=this
		// getSubject({limit:this.limit,page:1,keyword:this.input,orderByCreateTimeAsc:this.orderByCreateTimeAsc}).then(res=>{
		// 	that.listData=res.data.data
		// 	that.total=res.data.totalCount
		// })
		GetClass({"current":1, "size":12, "id":1}).then(res=>{
			that.listData=res.data[0]
			that.total=res.data[1].total
		})
		// GetClass({"current":1, "size":3, "name":"老师1"}).then(res=>{
			
		// })
	},
	methods:{
		start(item){
			localStorage.setItem("nowMyclass",JSON.stringify(item))
			// 开始课程
			this.$router.push('/student/sec/classSec')
		},
		search(){
			let that=this
			this.cpage=0
			getSubject({keyword:this.input,limit:this.limit,page:1,orderByCreateTimeAsc:this.orderByCreateTimeAsc}).then(res=>{
				that.listData=res.data.data
				that.total=res.data.totalCount
			})
		},
		current(val){
			let that=this
			GetClass({"current":val, "size":12, "id":1}).then(res=>{
			that.listData=res.data[0]
			that.total=res.data[1].total
		})
			// getSubject({keyword:this.input,limit:this.limit,page:val,orderByCreateTimeAsc:this.orderByCreateTimeAsc}).then(res=>{
			// 	that.listData=res.data.data
			// 	that.total=res.data.totalCount
			// })
		},
		sort(){
			let that=this
			if(this.orderByCreateTimeAsc==0){
				this.orderByCreateTimeAsc=1
			} else{
				this.orderByCreateTimeAsc=0
			}
			getSubject({keyword:this.input,limit:this.limit,page:1,orderByCreateTimeAsc:this.orderByCreateTimeAsc}).then(res=>{
				that.listData=res.data.data
				that.total=res.data.totalCount
			})
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/classPC.css';
</style>



